html {
	height: 100%;
}

body {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	background-color: rgba(58, 64, 71, 255);
	padding: 2em 4em 0.5em 2em;
}

/* NORMALIZATION */

*, ::after, ::before {
	box-sizing: border-box;
}

table {
	border-collapse: collapse;
}

h1, h2, h3, h4, h5 {
	margin: 0.5em 0;
}

button, input, optgroup, select, textarea {
	margin: 0;
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

#root {
	height: 100%;
}

.containter {
	display: flex;
	column-gap: 2em;
}

.content {
	flex: 0.8;
	color: white;
}



@media (max-width: 800px) {
	.containter {
		flex-flow: column;
	}

	body {
		padding: 1em;
	}
}